form {
    text-align: left;
  }
  
  form label,
  label input,
  label select {
    display: block;
    text-align: left;
    color: #707070;
  }
  
  label input,
  label select {
    width: 100%;
    padding: 15px;
    margin-top: 10px;
    border: rgba(112, 112, 112, 0.25);
  }
  
  form label {
    margin-top: 20px;
  }
  
  form button {
    border-radius: 5px;
    background: #007ae6;
    color: #fff;
    border: none;
    -webkit-box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
    -moz-box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
    box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
    padding: 15px 30px;
    font-size: 16px;
    margin-top: 15px;
  }